.Year {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex: 1 1;
      flex: 1 1;
  position: relative;
}

.YearTitleContainer {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}

.YearTitle {
  position: absolute;
  font-size: 0.75em;
  cursor: default;
}

.DimYearTitle {
  color: rgb(41, 46, 47);
}

.HighlightYearTitle {
  color: white;
}

.YearSpent {
  /* background-color: transparent; */
  border-right: 0.01em solid white;
  border-bottom: 0.01em solid white;
}

.YearSpent .YearTitle {
  left: 2.2em;
  text-decoration: line-through;
  color: rgba(0, 0, 0, 0.05)
}

.YearLeft .YearTitle {
  right: 2.2em;
}

.YearLeft {
  background-color: #2d3436;
  color: rgb(115, 115, 115);
  border-right: 0.01em solid rgba(33, 39, 41, 0.4);
  border-bottom: 0.01em solid rgba(33, 39, 41, 0.5);
}

.YearProgress{
  background-color: white;
  -ms-flex-negative: 0;
      flex-shrink: 0;
  -ms-flex-positive: 0;
      flex-grow: 0;
}

.YearNotAlive {
  background-color: white;
  -ms-flex-negative: 0;
      flex-shrink: 0;
  -ms-flex-positive: 0;
      flex-grow: 0;
}

.YearSpent:hover {
  background-color: #ecf0f1;
}

.YearSpent:hover .YearTitle {
  color: rgba(0, 0, 0, 0.75)
}

.YearLeft:hover .YearTitle {
  color: rgb(115, 115, 115);
}

.YearLeft:hover .HighlightYearTitle {
  color: white;
}

